import myTransition from '../myTransition/myTransition.vue'
export default {
  data () {
    return {
      isTouched: false,
      timer: null
    }
  },
  methods: {
    start () {
      let self = this
      this.timer = setTimeout(function () {
        self.isTouched = true
      }, 300)
    },
    end () {
      clearTimeout(this.timer)
      this.isTouched = false
    }
  },
  props: ['isHot'],
  components: { myTransition }
}



