export default {
  data () {
    return {
      isAppreciated: false,
      appreciateNum: 8
    }
  },
  methods: {
    doAppreciate () {
      this.isAppreciated = !this.isAppreciated
      this.appreciateNum = this.isAppreciated ? this.appreciateNum + 1 : this.appreciateNum - 1
    }
  }
}



