import articleLink1 from '../articleLink1/articleLink1.vue'
export default {
  data () {
    return {
      title: '',
      isRotate: false,
      isShow: true
    }
  },
  props: ['msg'],
  created () {
    this.title = (this.msg === '首页') ? '热门推荐' : (this.msg + '热门')
  },
  methods: {
    refresh () {
      console.log(this.isRotate)
      if (this.isRotate === false) {
        let self = this
        this.isRotate = true
        setTimeout(function () {
          self.isRotate = false
        }, 1000)
      }
    },
    close () {
      this.isShow = false
    }
  },
  components: { articleLink1 }
}



